/* animação */
.imgFade{
  animation-duration: 500ms;
  animation-name: imgFade;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-play-state: paused;
}

.imgFadeReverse{
  animation-duration: 500ms;
  animation-name: imgFadeReverse;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-play-state: paused;
}

.imgRun {
  animation-duration: 500ms;
  animation-name: imgRun;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-play-state: paused;
}
.imgRunReverse {
  animation-duration: 500ms;
  animation-name: imgRunReverse;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay:  0s;
  animation-play-state: paused;
}

.imgStatic{
  opacity: 0%;
  margin-left: 0%;
}

.txtFadeReverse {
  animation-duration: 500ms;
  animation-name: txtFadeReverse;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-play-state: paused;
}
.txtFade {
  animation-duration: 500ms;
  animation-name: txtFade;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-play-state: paused;
}

.txtRun {
  animation-duration: 500ms;
  animation-name: txtRun;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-play-state: paused;
}
.txtRunReverse {
  animation-duration: 500ms;
  animation-name: txtRunReverse;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
  animation-timing-function: linear;
  animation-delay:  0s;
  animation-play-state: paused;
}

:root {
  --my-start-txtFade: 100%;
  --my-start-txtFadeReverse: 0%;

  --my-start-imgFade: 100%;
  --my-start-imgFadeReverse: 0%;
  
  --my-start-imgRun: 100vw;
  --my-start-imgRunReverse: 0%;

  --my-start-txtRun: 100vw;
  --my-start-txtRunReverse: 0%;
  
}

#txtId1{
  margin-right: 0%;
  position: absolute;
  width: 30vw;
  min-width: 10vw;
}
#txtId2 {
  margin-right: 100%;
  position: absolute;
  width: 30vw;
  min-width: 10vw;
}
#txtId3 {
  margin-right: 100%;
  position: absolute;
  width: 30vw;
  min-width: 10vw;
}
#txtId4 {
  margin-right: 100%;
  position: absolute;
  width: 30vw;
  min-width: 10vw;
}

#imagemId1 {
    margin-left: 0vw;
    width: 100vw;
    min-width: 100vh;
}
#imagemId2 {
    margin-left: 100vw;
    position: absolute;
    min-width: 100vh;
}
#imagemId3 {
    margin-left: 100vw;
    position: absolute;
    min-width: 100vh;
}
#imagemId4 {
    margin-left: 100vw;
    position: absolute;
    min-width: 100vh;
}



@keyframes txtRun {
  0% {
    margin-right: var(--my-start-txtRun);
  }
  100%{
    margin-right: 0%;
  }
  
}

@keyframes txtRunReverse {
  0%{
    margin-right: var(--my-start-txtRunReverse);
  }
  100%{
    margin-right: 100%;
  }
}

@keyframes txtFade {
  0%{
    opacity: var(--my-start-txtFade);
    margin-right: 0%;
  }
  100%{
    opacity: 0%;
    margin-right: 0%;
  }
  
}

@keyframes txtFadeReverse {
  0%{
    opacity:var(--my-start-txtFadeReverse);
    margin-right: 0%;
  }
  100%{
    opacity: 100%;
    margin-right: 0%;
  }
  
}


@keyframes imgRun {
  0% {
    /* transform: translateX(var(--my-start-imgRunMargin)); */

    margin-left: var(--my-start-imgRun);

  }
  100% {
    margin-left: 0%;
  }
}

@keyframes imgRunReverse {
  0% {

    margin-left: var(--my-start-imgRunReverse);
  }

  100% {
    margin-left: 100vw;
  }
}

@keyframes imgFade {
  0%{
    opacity: var(--my-start-imgFade);
    margin-left: 0%;
  }
  100%{
    opacity: 0%;
    margin-left: 0%;
  }
  
}

@keyframes imgFadeReverse {
  0%{
    opacity: var(--my-start-imgFadeReverse);
    margin-left: 0%;
  }
  100%{
    opacity: 100%;
    margin-left: 0%;
  }
  
}

@keyframes txtRun {
  0% {
    margin-right: var(--my-start-txtRun);
  }
  100%{
    margin-right: 0%;
  }
  
}






