.body {
  /* font */
  /* font-family: Gill Sans, sans-serif; */
  font-family: "Gill Sans", sans-serif;
  color: white;
  text-align: center;
  font-size: "20px";
}
.icons {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3700b3;
  border-radius: 5px;
  padding: 2% 0%;
  border: 1px solid white;
  margin: 15% 30%;
}

.fotoPerfil{
  border-radius: 100%;
  background: url("../../../../images/eu.jpg") no-repeat top;
  background-size: cover;
  background-color: "green";
  height: 25vh;
  width: 25vh;
  border: 2px white solid;
  position: absolute;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  display: flex;
  z-index: 4;

}

/* ANIMATION */

.images:hover {
  min-width: 110%;
}

.fotoPerfil:hover {
  /* background-size: 120% 120%; */
  height: 30VH;
  width: 30VH;
}

.designBox{
  /* border: 1px solid white; */
  border-radius: 5px;
  border-right:  1px solid white ;
  height: 20vh;
}

/* RESPONSIVE SET */

@media screen and (max-width: 540px) {
  /* PAGE 2 */
  .body {
    font-family: "Gill Sans", sans-serif;
    color: white;
    text-align: center;
    font-size: 50%;
  }
  .subTitle {
    font-family: "Gill Sans", sans-serif;
    color: white;
    text-align: center;
    border-block-end: 1px solid white;
    margin-bottom: 10%;
    font-size: 0px;
  }
  .titleText {
    font-family: "Gill Sans", sans-serif;
    color: white;
    text-align: center;
    font-size: 70%;
  }
  .subTitleCard {
    font-family: "Gill Sans", sans-serif;
    color: white;
    text-align: center;
    margin-bottom: 2px;
    font-size: 0px;
  }
  .icons {
    padding: 15% 0%;
  }

  /* PAGE 3 */

  .titleTextDesc {
    margin-top: 30%;
    font-family: "Gill Sans", sans-serif;
    color: white;
    text-align: center;
    font-size: 70%;
  }
  .bodyDesc {
    font-family: "Gill Sans", sans-serif;
    color: white;
    text-align: justify;
    font-size: 50%;
  }
}

