.main{
    position: sticky;
}

.banner{
    background: url("../../../../images/bannerContato.png") repeat fixed center;
    background-size: cover;
    background-attachment: local scroll;
    
    /* width: 10%; */
    height: 50vh;
    z-index: 2;
}

.img {
    max-width: 20%;
    max-height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.img:hover {
    max-width: 30%;
    max-height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.email{
    font-size: 10px;
}