.mainXp{
    position: fixed;
    height: 100vh;
    width: 100vw;
}

#blockImg{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    position: absolute;
    height: 100vh;
    width: 100vw;
    margin-right: 100%;
    background-color: black;
}

#img1{
    position: absolute;
    height: auto;
    max-height: 100vh;
    width: 100vw;

    /* background-size: cover; */

    margin-right:  var(--my-start-imgRunXp);
}

#img2{
    position: absolute;

    height: auto;
    max-height: 100vh;
    width: 100vw;

    background-size: cover;

    margin-right: var(--my-start-imgRunXp);
}

#txt1{

    position: absolute;
    height: 100%;
    width: 100%;

    margin-left: var(--my-start-txtRunXp);
}

#txt2{

    position: absolute;
    height: 100%;
    width: 100%;

    margin-left: var(--my-start-txtRunXp);
}

/* Animação */
.imgRunXp{
    animation-name: imgRunXp;
    animation-play-state: paused;
    
    animation-duration: 1000ms;
    animation-delay: 0s;
    animation-iteration-count: 1;
    
    animation-fill-mode: both;
    animation-direction: normal;
    animation-timing-function: linear;
}

.txtRunXp{
    animation-name: txtRunXp;
    animation-play-state: paused;
    
    animation-duration: 1000ms;
    animation-delay: 0s;
    animation-iteration-count: 1;
    
    animation-fill-mode: both;
    animation-direction: normal;
    animation-timing-function: linear;
}

.imgRunXpReverse{
    animation-name: imgRunXpReverse;
    animation-play-state: paused;
    
    animation-duration: 1000ms;
    animation-delay: 0s;
    animation-iteration-count: 1;
    
    animation-fill-mode: both;
    animation-direction: normal;
    animation-timing-function: linear;
}

.txtRunXpReverse{
    animation-name: txtRunXpReverse;
    animation-play-state: paused;
    
    animation-duration: 1000ms;
    animation-delay: 0s;
    animation-iteration-count: 1;
    
    animation-fill-mode: both;
    animation-direction: normal;
    animation-timing-function: linear;
}

:root{
    --my-start-imgRunXp: 200vw;
    --my-start-imgRunReverseXp: 0%;
  
    --my-start-txtRunXp: 100%;
    --my-start-txtRunReverseXp: 0%;
}

@keyframes imgRunXp{
    0%{
        margin-right: var(--my-start-imgRunXp);
    }
    100%{
        margin-right:0%;
    }
}

@keyframes imgRunXpReverse{
    0%{
        margin-right: var(--my-start-imgRunReverseXp);
    }
    100%{
        margin-right: 200vw;
    }
}

@keyframes txtRunXp{
    0%{
        margin-left: var(--my-start-txtRunXp);
    }

    100%{
        margin-left: 0%;
    }
}

@keyframes txtRunXpReverse{
    0%{
        margin-left: var(--my-start-txtRunReverseXp);
    }

    100%{
        margin-left: 100%;
    }
}

